@keyframes ldio-tkuchhxylxc {
  0% { transform: rotate(0) }
  100% { transform: rotate(360deg) }
}
.ldio-tkuchhxylxc div { box-sizing: border-box!important }
.ldio-tkuchhxylxc > div {
  position: absolute;
  width: 148px;
  height: 148px;
  top: 26px;
  left: 26px;
  border-radius: 50%;
  border: 20px solid #000;
  border-color: #ffffff transparent #ffffff transparent;
  animation: ldio-tkuchhxylxc 1s linear infinite;
}

.ldio-tkuchhxylxc > div:nth-child(2), .ldio-tkuchhxylxc > div:nth-child(4) {
