}
.ldio-tkuchhxylxc > div:nth-child(3) { border-color: transparent }
.ldio-tkuchhxylxc > div:nth-child(3) div {
  position: absolute;
  width: 100%;
  height: 100%;
  transform: rotate(45deg);
}
.ldio-tkuchhxylxc > div:nth-child(3) div:before, .ldio-tkuchhxylxc > div:nth-child(3) div:after { 
  content: "";
  display: block;
  position: absolute;
  width: 20px;
  height: 20px;
  top: -20px;
  left: 44px;
  background: #ffffff;
  border-radius: 50%;
  box-shadow: 0 128px 0 0 #ffffff;
}
.ldio-tkuchhxylxc > div:nth-child(3) div:after {
